	getData(){
		return this.data
	}
	getPageData(page, id) {
		let view = ""
		if(page === "life") {
			view = "life_data"
		} else if (page === "supporter") {
			view = "supporter_data"
		} else if (page === "events") {
			view = "event_data"
		}
		return this.data[view][id - 1]
	}
